body {
  margin: 0;
  
  
}


.top-pane{
  background-color: hsl(225, 6%, 25%);
}
.pane{
  height: 50vh;
  display: flex;

}
.editor-cont{
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: .5rem;
  background-color: hsl(225, 6%, 25%);

}
.editor-cont.collapsed{
  flex-grow: 0;
}
.editor-cont.collapsed .CodeMirror-scroll{
  position: absolute;
  overflow: hidden !important;
}
.expand-collapse-btn{
  margin-left: .5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}
.editor-tital{
  display: flex;
  justify-content: space-between;
  background-color: hsl(225, 4%, 18%);
  color: white;
  padding: .5rem .5rem .5rem 1rem;
  border-top-right-radius: .5rem;
  border-top-left-radius: .5rem;
}
.codeMirror{
  height: 100% !important;
}
.code-mirror-wrapper{
  flex-grow: 1;
  border-bottom-right-radius: .5rem;
  border-bottom-left-radius: .5rem;
  overflow: hidden;
}
